import Vue from 'vue';
export default {
  SCROLLTOP: (state, data) => {
    state.scrollTop = data
  },
  BODYWIDTH: (state, data) => {
    state.bodyWidth = data
  },
  LANG: (state, data) => {
    state.lang=data;
  },
  COMMITMONEY:(state,data)=>{
    state.money=data;
  },
  GETNEWSITEM:(state,data)=>{
    state.newsItem=data;
  },
  PUBLICSITEM:(state,data)=>{
    state.publicsItem=data;
    console.log(data);
  },
  PUBLICMESSAGE:(state,data)=>{
    state.publicMessage=data;

  },
  GETINDEXFRIENDS:(state,data)=>{
    state.indexFriends=data;
  },
  GETJOINJSON:(state,data)=>{
    state.joinJson=data;
  },
  ALERTOPEN:(state,data)=>{
    state.joinOpen?state.joinOpen=false:state.joinOpen=true;
    data?state.joinId=data:state.joinId="";
  },
  PLACECHANGE:(state,data)=>{
    state.joinPlace=data;
  },
  NEWSINDEX:(state,data)=>{
    state.newsIndex=data;
  },
}


