import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex);


  const store = new Vuex.Store({
    state: {
      scrollTop:0,
      bodyWidth:document.body.clientWidth ,
      lang:"cn",
      money:null,
      apiurl:'https://bitpie.songchenwen.com',
      newsItem:null,
      publicsItem:null,
      publicMessage:null,
      indexFriends:null,
      joinJson:null,
      joinOpen:false,
      joinId:"",
      joinPlace:"bj",
      newsIndex:0,
    },
    actions,
    mutations,
    getters
  });
export default store


